___CSS_LOADER_EXPORT___.push([module.id, `/* Responsive Tablet & Mobile */
@media (max-width: 770px) {
    /* Nav */

    label.logo {
        padding: 0 10%;
    }

    .drawer {
        display: block;
    }

    .menu {
        position: fixed;
        z-index: 1;
        width: 100%;
        height: auto;
        background: #B5CB99;
        top: 100px;
        left: -100%;
        text-align: center;
        transition: all .5s;
        margin: 0 !important;
    }

    .show-nav {
        left: 0;
    }

    nav ul li {
        display: block;
        margin: 40px 0;
        line-height: 30px;
    }

    nav ul li a {
        font-size: 20px;
        padding: 16px 20px;
        color: black;
    }

    a:hover {
        color: #186F65;
    }

    a#drawer:visited {
        color: white;
    }

    /* Jumbotron */

    .jumbotron {
        height: 300px;
    }

    .jumbotron h1 {
        font-size: 40px;
    }

    .jumbotron h2 {
        font-size: 20px;
    }

    /* Grid */
    .grid {
        grid-template-columns: repeat(2, 1fr);
        margin: 10%;
        margin-top: 0;
        padding: 0;
    }

    /* Footer */
    footer {
        margin-top: 50px;
    }

    img.resto-image {
        width: 500px;
        height: auto;
    }

    
}

@media (max-width: 715px) {
    .grid {
        grid-template-columns: repeat(1, 1fr);
        margin: 20%;
        margin-top: 0;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/tablet.css"],"names":[],"mappings":"AAAA,+BAA+B;AAC/B;IACI,QAAQ;;IAER;QACI,cAAc;IAClB;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,eAAe;QACf,UAAU;QACV,WAAW;QACX,YAAY;QACZ,mBAAmB;QACnB,UAAU;QACV,WAAW;QACX,kBAAkB;QAClB,mBAAmB;QACnB,oBAAoB;IACxB;;IAEA;QACI,OAAO;IACX;;IAEA;QACI,cAAc;QACd,cAAc;QACd,iBAAiB;IACrB;;IAEA;QACI,eAAe;QACf,kBAAkB;QAClB,YAAY;IAChB;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,YAAY;IAChB;;IAEA,cAAc;;IAEd;QACI,aAAa;IACjB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;;IAEA,SAAS;IACT;QACI,qCAAqC;QACrC,WAAW;QACX,aAAa;QACb,UAAU;IACd;;IAEA,WAAW;IACX;QACI,gBAAgB;IACpB;;IAEA;QACI,YAAY;QACZ,YAAY;IAChB;;;AAGJ;;AAEA;IACI;QACI,qCAAqC;QACrC,WAAW;QACX,aAAa;IACjB;AACJ","sourcesContent":["/* Responsive Tablet & Mobile */\r\n@media (max-width: 770px) {\r\n    /* Nav */\r\n\r\n    label.logo {\r\n        padding: 0 10%;\r\n    }\r\n\r\n    .drawer {\r\n        display: block;\r\n    }\r\n\r\n    .menu {\r\n        position: fixed;\r\n        z-index: 1;\r\n        width: 100%;\r\n        height: auto;\r\n        background: #B5CB99;\r\n        top: 100px;\r\n        left: -100%;\r\n        text-align: center;\r\n        transition: all .5s;\r\n        margin: 0 !important;\r\n    }\r\n\r\n    .show-nav {\r\n        left: 0;\r\n    }\r\n\r\n    nav ul li {\r\n        display: block;\r\n        margin: 40px 0;\r\n        line-height: 30px;\r\n    }\r\n\r\n    nav ul li a {\r\n        font-size: 20px;\r\n        padding: 16px 20px;\r\n        color: black;\r\n    }\r\n\r\n    a:hover {\r\n        color: #186F65;\r\n    }\r\n\r\n    a#drawer:visited {\r\n        color: white;\r\n    }\r\n\r\n    /* Jumbotron */\r\n\r\n    .jumbotron {\r\n        height: 300px;\r\n    }\r\n\r\n    .jumbotron h1 {\r\n        font-size: 40px;\r\n    }\r\n\r\n    .jumbotron h2 {\r\n        font-size: 20px;\r\n    }\r\n\r\n    /* Grid */\r\n    .grid {\r\n        grid-template-columns: repeat(2, 1fr);\r\n        margin: 10%;\r\n        margin-top: 0;\r\n        p