___CSS_LOADER_EXPORT___.push([module.id, `/* Mobile Responsive style */

@media (max-width: 425px) {
    .jumbotron h1 {
        font-size: 26px;
    }

    .jumbotron h2 {
        font-size: 60%;
    }

    /* Grid */
    .grid {
        grid-template-columns: repeat(1, 1fr);
        margin: 25%;
        margin-top: 0;
        padding: 0;
    }

    .grid>article {
        width: 200px;
    }

    .grid>article img {
        width: 200px;
    }

    /* Footer */
    footer {
        font-size: 10px;
    }

    img.resto-image {
        width: 250px !important;
        height: auto;
        border-radius: 8px;
    }

    #inputName {
        margin-inline-start: 0 !important;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/mobile.css"],"names":[],"mappings":"AAAA,4BAA4B;;AAE5B;IACI;QACI,eAAe;IACnB;;IAEA;QACI,cAAc;IAClB;;IAEA,SAAS;IACT;QACI,qCAAqC;QACrC,WAAW;QACX,aAAa;QACb,UAAU;IACd;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,YAAY;IAChB;;IAEA,WAAW;IACX;QACI,eAAe;IACnB;;IAEA;QACI,uBAAuB;QACvB,YAAY;QACZ,kBAAkB;IACtB;;IAEA;QACI,iCAAiC;IACrC;AACJ","sourcesContent":["/* Mobile Responsive style */\r\n\r\n@media (max-width: 425px) {\r\n    .jumbotron h1 {\r\n        font-size: 26px;\r\n    }\r\n\r\n    .jumbotron h2 {\r\n        font-size: 60%;\r\n    }\r\n\r\n    /* Grid */\r\n    .grid {\r\n        grid-template-columns: repeat(1, 1fr);\r\n        margin: 25%;\r\n        margin-top: 0;\r\n        padding: 0;\r\n    }\r\n\r\n    .grid>article {\r\n        width: 200px;\r\n    }\r\n\r\n    .grid>article img {\r\n        width: 200px;\r\n    }\r\n\r\n    /* Footer */\r\n    footer {\r\n        font-size: 10px;\r\n    }\r\n\r\n    img.resto-image {\r\n        width: 250px !important;\r\n        height: auto;\r\n        border-radius: 8px;\r\n    }\r\n\r\n    #inputName {\r\n        margin-inline-start: 0 !important;\r\n    }\r\n}"],"sourceRoot":""}]);
